@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap);
.navbar{
    width: 100%;
    max-height: 80px;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #101524;
}

.navbar li{
    list-style-type: none;
    padding: 20px;
    height: 80px;
    width: 150px;
    display: flex;
    align-items: center;
    justify-content: center;

}

.navbar li:hover{
    background : #44e2e2;
    cursor: pointer;
  
}

.navbar li a{
    text-decoration: none;
    color: #fff;
    font-weight: 600;
    font-size: 1.2rem;
}





*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Shadows Into Light', cursive;
}

body{
    background : #44e2e2;
}
h1{
    text-align: center;
    margin-top: 40px;
    margin-bottom: 1em;
}

.container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    margin-bottom: 2em;
}


.card{
    width: 350px;
    height: auto;
    background-color:#0C1B38 ;
    border-radius: 5px;
    margin-bottom: 2em;
}

.image{
    width: 95%;
    height: 250px;
    background-color: black;
    position: relative;
}

img{
    width: 100%;
    height: 100%;
    position: absolute;
    border-radius: 5px;
    background-position: center;
    background-image: cover;
    bottom:6%;
    left: 8px;
}

h2{
    padding: 10px 20px;
    color: #fff;
    margin-bottom: 1em;
    text-transform: uppercase;
}

p {
    opacity: 0;
    position: absolute;
    bottom: 0%;
    left: 5%;
    color: #fff;
  }

  .card:hover p {
    opacity: 5;
    bottom: 25%;
    transition: all 0.8s ease-in-out;
  }

  .card:hover img{
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    transition: all 0.8s ease-in-out;

  }


